import React, { useState, useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import "./photos.css";
import app from "./firebaseConfig";
import { getFirestore, doc, getDoc, setDoc } from "firebase/firestore";
import { getStorage, ref, uploadBytesResumable, getDownloadURL } from "firebase/storage";
import { useRecoilValue } from "recoil";
import { loginAtom } from "./loginAtom";

function EditTrip() {
  const db = getFirestore(app); //   
  const storage = getStorage(app); //     
  let isLogined = useRecoilValue(loginAtom);
  const navigate = useNavigate();
  const { docId } = useParams();

  //  
  let [location1, setLocation1] = useState("");
  let [date1, setDate1] = useState("");
  let [comment, setComment] = useState("");
  let [photoURL, setPhotoURL] = useState("");
  let [image, setImage] = useState(null); //   

  useEffect(() => {
    const getData = async () => {
      const querySnapshot = await getDoc(doc(db, "tourMemo", docId)); // : tourMemo
      const ob = querySnapshot.data();
      setLocation1(ob.location);
      setComment(ob.comment);
      setDate1(ob.date);
      setPhotoURL(ob.photoURL);
    };

    getData();
  }, [db, docId]);

  const locHandle = (e) => {
    setLocation1(e.target.value);
  };

  const dateHandle = (e) => {
    setDate1(e.target.value);
  };

  const commentHandle = (e) => {
    setComment(e.target.value);
  };

  const handleReset = () => {
    setLocation1("");
    setDate1("");
    setComment("");
    setImage(null);
  };

  const storeHandle = async (e) => {
    e.preventDefault();

    if (!isLogined) {
      alert("   .");
      return;
    }
    if (image == null) return;

    const storageRef = ref(storage, "images/" + image.name);
    let photoURL = null;

    const uploadTask = uploadBytesResumable(storageRef, image);

    uploadTask.on(
      "state_changed",
      (snapshot) => {},
      (error) => {
        console.log(error);
      },
      () => {
        getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
          photoURL = downloadURL;

          setDoc(doc(db, "tourMemo", docId), {
            location: location1,
            date: date1,
            comment,
            photoURL,
          });

          setLocation1("");
          setDate1("");
          setComment("");
          setImage(null);

          alert("  .");
          navigate("/photos");
        });
      }
    );
  };

  return (
    <>
      <h1 style={{ marginInline: "2rem" }}>
        [  ]   .
      </h1>
      <h3 style={{ color: "red", marginInline: "2rem" }}>
        ) Image      .
      </h3>
      <form>
        <div className="tourContainer">
          <div>  </div>
          <input
            type="text"
            id=""
            onChange={locHandle}
            value={location1}
            style={{ lineHeight: "1.6em" }}
          />
          <div style={{ marginTop: "0.7em" }}> </div>
          <input
            type="date"
            id="date"
            onChange={dateHandle}
            value={date1}
          />
          <div style={{ marginTop: "0.7em" }}>  </div>
          <textarea
            cols="40"
            id=""
            onChange={commentHandle}
            value={comment}
          />
          <img src={photoURL} alt=" " width="300" />
          <div style={{ marginTop: "0.7em" }}>  ()</div>
          <input
            type="file"
            id="file"
            onChange={(e) => {
              setImage(e.target.files[0]);
            }}
          />
          <div
            style={{
              display: "inline-block",
              marginTop: "0.7em",
              fontSize: "24px",
            }}
          >
            <button
              type="submit"
              onClick={storeHandle}
              style={{
                color: "white",
                backgroundColor: "blue",
              }}
            >
                
            </button>
            &nbsp;
            <input
              type="reset"
              value=""
              onClick={handleReset}
            />
          </div>
        </div>
      </form>
    </>
  );
}

export default EditTrip;
