import React, { useState } from "react";
import { getFirestore, collection, addDoc } from "firebase/firestore";
import app from "./firebaseConfig";
import "./App.css";
import {
  getStorage,
  ref,
  uploadBytesResumable,
  getDownloadURL,
} from "firebase/storage";
import { useRecoilValue } from "recoil";
import { loginAtom } from "./loginAtom";

const Tour = () => {
  // Firestore Storage 
  const db = getFirestore(app);
  const storage = getStorage(app);
  const isLogined = useRecoilValue(loginAtom);

  //  
  const [location1, setLocation1] = useState("");
  const [date1, setDate1] = useState("");
  const [comment, setComment] = useState("");
  const [image, setImage] = useState(null); //   

  //   
  const locHandle = (e) => setLocation1(e.target.value);
  const dateHandle = (e) => setDate1(e.target.value);
  const commentHandle = (e) => setComment(e.target.value);
  const handleReset = () => {
    setLocation1("");
    setDate1("");
    setComment("");
    setImage(null);
  };

  //    
  const storeHandle = async (e) => {
    e.preventDefault();

    if (!isLogined) {
      alert("   .");
      return;
    }

    if (!image) {
      alert("  .");
      return;
    }

    try {
      // Storage  
      const storageRef = ref(storage, `images/${image.name}`);
      const uploadTask = uploadBytesResumable(storageRef, image);

      uploadTask.on(
        "state_changed",
        (snapshot) => {
          const progress =
            (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
          console.log(`Upload is ${progress}% done`);
        },
        (error) => {
          console.error("   :", error);
          alert(` : ${error.message}`);
        },
        async () => {
          //     URL 
          const downloadURL = await getDownloadURL(uploadTask.snapshot.ref);
          console.log("File available at", downloadURL);

          // Firestore  
          await addDoc(collection(db, "memo"), {
            location: location1,
            date: date1,
            comment,
            photoUrl: downloadURL, //   photoUrl  
          });

          //  
          handleReset();
          alert("    .");
        }
      );
    } catch (error) {
      console.error("   :", error);
      alert(`  : ${error.message}`);
    }
  };

  return (
    <div>
      <h1 style={{ textAlign: "center", marginTop: "100px", color: "brown" }}>
          
        <span style={{ fontSize: "16px" }}>(  )</span>
      </h1>
      <form>
        <div className="tourContainer">
          <div>  </div>
          <input
            type="text"
            id=""
            onChange={locHandle}
            value={location1}
            placeholder=" "
            style={{ lineHeight: "1.6em" }}
          />
          <div style={{ marginTop: "0.7em" }}> </div>
          <input
            type="date"
            id="date"
            onChange={dateHandle}
            value={date1}
          />
          <div style={{ marginTop: "0.7em" }}>  </div>
          <textarea
            cols="40"
            id=""
            onChange={commentHandle}
            value={comment}
            placeholder="   "
          />
          <div style={{ marginTop: "0.7em" }}>   </div>
          <input
            type="file"
            id="file"
            onChange={(e) => setImage(e.target.files[0])}
          />
          <div
            style={{
              display: "inline-block",
              marginTop: "0.7em",
              fontSize: "24px",
            }}
          >
            <button
              type="submit"
              onClick={storeHandle}
              style={{
                color: "white",
                backgroundColor: "blue",
              }}
            >
               
            </button>
            &nbsp;
            <input type="reset" value="" onClick={handleReset} />
          </div>
        </div>
      </form>
    </div>
  );
};

export default Tour;
