import React, { useState, useRef } from "react";
import { useNavigate } from "react-router-dom";
import app from "./firebaseConfig"; // Firebase 
import "./Login.css";
import {
  getAuth,
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  signOut,
} from "firebase/auth";
import { loginAtom } from "./loginAtom";
import { useRecoilState } from "recoil";

const Login = () => {
  const [isLogined, setIsLogined] = useRecoilState(loginAtom);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const pwRef = useRef(); //   
  const navigate = useNavigate(); //  
  const auth = getAuth(app);

  //   
  const emailChangeHandle = (e) => {
    setEmail(e.target.value);
  };

  //   
  const passwordChangeHandle = (e) => {
    setPassword(e.target.value);
  };

  //   
  const signUpHandle = (e) => {
    e.preventDefault();

    if (!password || password.length < 6) {
      alert(" 6  .");
      pwRef.current.focus();
      return;
    }

    createUserWithEmailAndPassword(auth, email, password)
      .then((userCredential) => {
        const user = userCredential.user;
        console.log(user);
        alert(" .");
        setEmail("");
        setPassword("");
      })
      .catch((error) => {
        console.error(" :", error);
        alert(` : ${error.message}`);
      });
  };

  //   
  const signInHandle = (e) => {
    e.preventDefault();

    signInWithEmailAndPassword(auth, email, password)
      .then((userCredential) => {
        const user = userCredential.user;
        console.log(" :", user.uid);
        setIsLogined(true);
        alert(" .");
        setEmail("");
        setPassword("");
        navigate("/"); //  
      })
      .catch((error) => {
        console.error(" :", error);
        alert(` : ${error.message}`);
      });
  };

  //   
  const logOutHandle = () => {
    signOut(auth)
      .then(() => {
        setIsLogined(false);
        alert(" .");
        navigate("/login");
      })
      .catch((error) => {
        console.error(" :", error);
        alert(` : ${error.message}`);
      });
  };

  return (
    <div className="loginPage">
      <h2>Email/Password </h2>
      <form className="loginForm">
        <label>
          &nbsp;&nbsp; e-mail &nbsp;&nbsp;:{" "}
          <input
            type="text"
            value={email}
            onChange={emailChangeHandle}
            id="email"
          />
        </label>
        <label>
          password :{" "}
          <input
            type="password"
            ref={pwRef}
            value={password}
            onChange={passwordChangeHandle}
            id="password"
          />
        </label>
        <p>
          {isLogined ? (
            <button type="button" onClick={logOutHandle}>
              
            </button>
          ) : (
            <button type="button" onClick={signInHandle}>
              
            </button>
          )}
          &nbsp;
          <button type="button" id="register" onClick={signUpHandle}>
            
          </button>
        </p>
      </form>
    </div>
  );
};

export default Login;
